<template>
  <div class="newfamily">
      <yd-navbar title="New family">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon>back</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>

        <yd-cell-group title="Family Name:">
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" placeholder="" v-model="familyInfo.family_name"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        
        <yd-cell-group title="Roomie Number:">
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" placeholder="" v-model="familyInfo.roomie_number"></yd-input>
            </yd-cell-item>
        </yd-cell-group>

        

    <yd-button-group>
        <yd-button size="large" @click.native="updateFamilyInfo" type="primary">Save</yd-button>
    </yd-button-group>

  </div>
</template>
<script>
import cookie from '../../static/js/cookie';
import {APIstartfamily} from '../../api/api'
export default {
 data () {
     return {
         familyInfo : {
             apartment:'',
             roomie_number :'2',
             roomie_id : '1',
             district : 'changning',
             subwayline : '2',
             family_name: "Fatboy's club",
             tag:'washing',
            //  apartment_host:''
         }
     }
 },
 methods: {
     getUsername() {
     },
     updateFamilyInfo(){
         APIstartfamily(this.familyInfo).then().catch()
     }
 },
 mounted () {
     this.getUsername()
 }
}
</script>



